</template>
<script>
import { useGenreStore } from '@/stores/genreStore';



export default 
{
    data() {
        return {
            query : '',
            mediaList : [],
            searchMedia : 'Movie',
            show_ChosenMedia : false,
            chosenMedia : null,
            currentPage : 1,
            totalPages : 1,
            showPageMultiplier : 1,
        };
    },
    computed: {
        genreStore(){
            return useGenreStore();
        },
        page1(m){
            m = this.showPageMultiplier;
            return (4 * m) - 3
        },
        page2(m){
            m = this.showPageMultiplier;
            return (4 * m) - 2
        },
        page3(m){
            m = this.showPageMultiplier;
            return (4 * m) - 1
        },
        page4(m){
            m = this.showPageMultiplier;
            return (4 * m)
        },
    },
    methods: {
        async search(newSearch){
            if (this.query.length < 2){
                return
            }

            this.show_ChosenMedia = false;
            this.chosenMedia = null;

            if (newSearch == true){
                this.currentPage = 1;
            }
            

            if (this.searchMedia == "Movie"){
                try{
                    const response = await fetch(`http://localhost:8000/search-movie/?title=${this.query}&page=${this.currentPage}`);

                    if (!response.ok){
                        throw new Error('Failed to fetch data');
                    }
                    const data = await response.json();
                    this.mediaList = data.movies || [];
                    this.currentPage = data.current_page || 1;
                    this.totalPages = data.total_pages || 1;
                }
                catch (error){
                    console.error('error catching data', error)
                }
            }
            else if (this.searchMedia == "TV Show"){
                try{
                    const response = await fetch(`http://localhost:8000/search-show/?title=${this.query}&page=${this.currentPage}`);

                    if (!response.ok){
                        throw new Error('Failed to fetch data');
                    }
                    const data = await response.json();
                    this.mediaList = data.shows || [];
                    this.currentPage = data.current_page || 1;
                    this.totalPages = data.total_pages || 1;
                }
                catch (error){
                    console.error('error catching data', error)
                }
            }
            else if (this.searchMedia == "Book"){
                try{
                    const response = await fetch(`http://localhost:8000/search-book/?title=${this.query}&page=${this.currentPage}`);
                    if (!response.ok){
                        throw new Error('Failed to fetch data');
                    }
                    const data = await response.json();
                    this.mediaList = data.books || [];
                    this.currentPage = data.current_page || 1;
                    this.totalPages = data.total_pages || 1;
                    console.log(data);
                }
                catch (error){
                    console.error('error catching data', error)
                }
            }

        },
        toggleMedia(media){
            this.currentPage = 1;
            this.showPageMultiplier = 1;
            this.show_ChosenMedia = false;
            this.searchMedia = media;
            this.mediaList = [];
            this.search(true);
        },
        clearQuery(){
            this.currentPage = 1;
            this.query = '';
            this.mediaList = [];
            this.chosenMedia = null;
            this.show_ChosenMedia = false;
        },
        select(media){
            this.show_ChosenMedia = true;
            this.chosenMedia = media;
            this.mediaList = [];
            this.query = media.title || media.name || media.volumeInfo['title'];
            this.currentPage = 1;
        },
        getGenreName(id){
            return this.genreStore.getGenreById(id);

        },
        getGenreColor(id){
            return this.genreStore.getGenreColorById(id);
        },
        changePage(pageNumber){
            this.currentPage = pageNumber;
            this.search(false);
        },
        nextPage(){
            if (this.page4 == this.currentPage){
                this.showPageMultiplier++;
                this.changePage(this.page1);
            }
            else{
                this.changePage(Number(this.currentPage) + 1);
            }
        },
        prevPage(){
            if (this.page1 == this.currentPage){
                this.showPageMultiplier--;
                this.changePage(this.page4);
            }
            else{
                this.changePage(Number(this.currentPage) - 1);
            }
        }
    },};
</script>
